<!-- *************************************************************************
     TEMPLATE
     ************************************************************************* -->

<template lang="pug">
popover-layout(
  @close="onClose"
  :class=`[
    "c-instagram-popover",
    "c-instagram-popover--" + theme
  ]`
  :closed="closed"
  :editor="editor"
  :position="position"
  :subscribed="subscribed"
  :theme="theme"
  :triggered="triggered"
)
  div(
    class="c-instagram-popover__header"
    slot="header"
  )
    img(
      @click="onGoToInstagram"
      :src="data.picture"
      class="c-instagram-popover__picture"
    )

    .c-instagram-popover__information
      .c-instagram-popover__account
        span(
          @click="onGoToInstagram"
          class="c-instagram-popover__name"
        ) {{ data.name }}

        span(
          v-if="showVerifiedBadge"
          class="c-instagram-popover__verified"
        )

      p(
        v-if="showBiography && data.biography"
        v-html="formattedBiography"
        class="c-instagram-popover__biography"
      )

      instagram-follow(
        @click="onGoToInstagram"
        class="c-instagram-popover__follow u-animated u-zoom-in u-ms250 u-delay250"
        size="small"
      ) Follow us on Instagram

  div(
    v-if="showStatistics"
    class="c-instagram-popover__statistics"
    slot="body"
  )
    instagram-statistic(
      v-for="(statistic, type) in data.statistics"
      @click="onGoToInstagram"
      :abbreviate="statistic.abbreviate"
      :label="statistic.label"
      :separator="statistic.separator"
      :key="type"
      :theme="theme"
      :value="statistic.value"
    )

  instagram-stories(
    v-if="showStories && data.stories.length > 0"
    :stories="data.stories"
    :theme="theme"
    class="c-instagram-popover__stories"
    slot="body"
  )

  instagram-medias(
    v-if="showMedias && data.medias.length > 0"
    :medias="data.medias"
    :theme="theme"
    class="c-instagram-popover__medias"
    slot="body"
  )
</template>

<!-- *************************************************************************
     SCRIPT
     ************************************************************************* -->

<script>
// TODO: SIZE
// TODO: LINK WITH INSTAGRAM API WHEN ACCEPTED
// TODO: ABILITY TO CONTROL MOST THINGS
// TODO: RELEASE ON PRODUCTION

// NPM
import axios from "axios"

// PROJECT: COMPONENTS
import InstagramFollow from "@/components/instagram/InstagramFollow"
import InstagramMedias from "@/components/instagram/InstagramMedias"
import InstagramStatistic from "@/components/instagram/InstagramStatistic"
import InstagramStories from "@/components/instagram/InstagramStories"
import PopoverLayout from "@/components/layouts/PopoverLayout"

// PROJECT: FILTERS
import { truncate } from "@/filters/text"

// PROJECT: MIXINS
import CloseMixin from "@/mixins/CloseMixin"
import DirectionMixin from "@/mixins/DirectionMixin"
import EditorMixin from "@/mixins/EditorMixin"
import PositionMixin from "@/mixins/PositionMixin"
import SubscriptionMixin from "@/mixins/SubscriptionMixin"
import ThemeMixin from "@/mixins/ThemeMixin"
import TriggerMixin from "@/mixins/TriggerMixin"
import WidgetMixin from "@/mixins/WidgetMixin"

export default {
  name: "InstagramPopover",

  components: {
    InstagramFollow,
    InstagramMedias,
    InstagramStatistic,
    InstagramStories,
    PopoverLayout
  },

  mixins: [
    CloseMixin,
    DirectionMixin,
    EditorMixin,
    PositionMixin,
    SubscriptionMixin,
    ThemeMixin,
    TriggerMixin,
    WidgetMixin
  ],

  props: {
    accessToken: {
      type: String,
      required: true
    },
    instagramId: {
      type: String,
      required: true
    },
    showBiography: {
      type: Boolean,
      default: true
    },
    showMedias: {
      type: Boolean,
      default: true
    },
    showStatistics: {
      type: Boolean,
      default: false
    },
    showStories: {
      type: Boolean,
      default: false
    },
    showVerifiedBadge: {
      type: Boolean,
      default: true
    }
  },

  data: () => ({
    // --> STATE <--

    data: {
      username: "",
      name: "",
      biography: "",
      picture: "",

      statistics: {
        medias: {
          label: "Posts",
          value: 0,
          separator: true
        },
        followers: {
          label: "Followers",
          value: 0,
          abbreviate: true
        },
        follows: {
          label: "Following",
          value: 0
        }
      },

      stories: [],

      medias: []
    },

    endpoints: {
      user:
        "https://graph.facebook.com/v3.2/{instagramId}?fields={fields}&access_token={accessToken}",
      userMedias:
        "https://graph.facebook.com/v3.2/{instagramId}/media?access_token={accessToken}",
      userStories:
        "https://graph.facebook.com/v3.2/{instagramId}/stories?access_token={accessToken}"
    }
  }),

  computed: {
    formattedBiography() {
      // Wrap each hashtag with a link
      let biography = truncate(this.data.biography, 55)

      biography = biography.replace(
        /(^|\W)(#[a-z\d][\w-]*)/gi,
        '$1<a class="c-instagram-popover__hashtag" href="https://www.instagram.com/explore/tags/$2/" target="_blank">$2</a>'
      )

      // Replace # character in hrefs
      biography = biography.replace(/\/tags\/#/g, "/tags/")

      return biography
    }
  },

  created() {
    this.fetchProfile()

    if (this.showMedias) {
      this.fetchMedias()
    }

    if (this.showStories) {
      this.fetchStories()
    }
  },

  methods: {
    // --> HELPERS <--

    async fetchProfile() {
      try {
        const { data } = await axios.get(
          this.endpoints.user
            .replace("{instagramId}", this.instagramId)
            .replace("{accessToken}", this.accessToken)
            .replace(
              "{fields}",
              "username,name,biography,profile_picture_url,media_count,followers_count,follows_count"
            )
        )

        this.data.username = data.username
        this.data.name = data.name
        this.data.biography = data.biography
        this.data.picture = data.profile_picture_url
        this.data.statistics.medias.value = data.media_count
        this.data.statistics.followers.value = data.followers_count
        this.data.statistics.follows.value = data.follows_count
      } catch (error) {
        console.error(error)
      }
    },

    async fetchMedias() {
      try {
        const { data } = await axios.get(
          this.endpoints.userMedias
            .replace("{instagramId}", this.instagramId)
            .replace("{accessToken}", this.accessToken)
        )
        this.data.medias = data
      } catch (error) {
        console.error(error)
      }
    },

    async fetchStories() {
      try {
        const { data } = await axios.get(
          this.endpoints.userStories
            .replace("{instagramId}", this.instagramId)
            .replace("{accessToken}", this.accessToken)
        )
        this.data.stories = data
      } catch (error) {
        console.error(error)
      }
    },

    // --> EVENT LISTENERS <--

    onGoToInstagram() {
      window.open(`https://www.instagram.com/${this.data.username}`, "_blank")
    }
  }
}
</script>

<!-- *************************************************************************
     STYLE
     ************************************************************************* -->

<style lang="scss">
// VARIABLES
$c: ".c-instagram-popover";

#{$c} {
  #{$c}__header,
  #{$c}__statistics,
  #{$c}__stories,
  #{$c}__medias {
    padding-right: 10px;
    padding-left: 10px;
  }

  #{$c}__statistics,
  #{$c}__stories,
  #{$c}__medias {
    border-top-width: 1px;
    border-top-style: solid;
  }

  #{$c}__header {
    display: flex;
    padding-top: 12px;
    padding-bottom: 12px;

    #{$c}__picture {
      flex: 0 0 auto;
      margin-right: 15px;
      width: 50px;
      height: 50px;
      border-width: 1px;
      border-style: solid;
      border-radius: 100%;
      user-select: none;
      cursor: pointer;
    }

    #{$c}__information {
      flex: 1;

      #{$c}__account {
        display: flex;
        align-items: center;

        #{$c}__name {
          font-weight: 600;
          font-size: 14px;
          line-height: 14px;
          cursor: pointer;

          &:hover {
            text-decoration: underline;
          }
        }

        #{$c}__verified {
          display: inline-block;
          margin-left: 5px;
          width: 14px;
          height: 14px;
          background-image: url(data:image/png;base64,);
          background-size: cover;
        }
      }

      #{$c}__biography {
        overflow: hidden;
        margin: 5px 0 0;
        max-height: 34px;
        font-size: 13px;
        line-height: 17px;

        #{$c}__hashtag {
          text-decoration: none;
          transition: color 125ms linear;
          cursor: pointer;

          &:hover {
            text-decoration: underline;
          }
        }
      }

      #{$c}__follow {
        margin-top: 9px;
      }
    }
  }

  #{$c}__statistics {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(3, 1fr);
    padding-top: 9px;
    padding-bottom: 9px;
  }

  // --> THEMES <--

  @each $theme in $themes {
    $themeName: map-get($theme, "name");

    &--#{$themeName} {
      #{$c}__statistics,
      #{$c}__stories,
      #{$c}__medias {
        border-top-color: mdg($theme, "borders", "default", "primary");
      }

      #{$c}__header {
        #{$c}__picture {
          border-color: mdg($theme, "borders", "default", "primary");

          @if ($themeName == "dark") {
            box-shadow: 0 1px 5px mdg($theme, "shadows", "default", "primary");
          }
        }

        #{$c}__information {
          #{$c}__biography {
            color: mdg($theme, "fonts", "default", "secondary");

            #{$c}__hashtag {
              color: mdg($theme, "fonts", "default", "secondary");

              &:hover {
                color: mdg($theme, "fonts", "default", "primary");
              }
            }
          }

          #{$c}__follow {
            @if ($themeName == "dark") {
              box-shadow: 0
                1px
                5px
                mdg($theme, "shadows", "default", "primary");
            }
          }
        }
      }

      #{$c}__statistics {
        background-color: mdg($theme, "backgrounds", "default", "tertiary");
      }
    }
  }
}
</style>
